import BackTop from "~/components/BackTop";
import AddNavPopup from "~/components/AddNavPopup";
import Header from "~/components/Header";
import WebsiteList from "~/components/WebsiteList";
import axios from "~/plugins/axios";
export default {
  components: {
    BackTop,
    AddNavPopup,
    Header,
    WebsiteList
  },
  data() {
    return {
      loading: false,
      isDrawer: false,
      active: "［前端］热门推荐",
      data: [],
      categorys: [],
      defaultOpeneds: ["0"],
      selfIndex: 0,
      isLeftbar: true,
      dialogFormVisible: false
    };
  },
  methods: {
    async getCategoryList() {
      const { data } = await this.$api.getCategoryList();
      this.categorys = data.data;

      if (this.categorys.length) {
        const { children } = this.categorys.slice(0, 1)[0];
        const categoryId = children[0]._id;
        this.findNav(categoryId);
      }
    },
    dataScroll() {
      const that = this;
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      let allSite = document.querySelectorAll(".box");
      for (let i = 0; i < allSite.length; i++) {
        if (scrollTop >= allSite[i].offsetTop) {
          that.selfIndex = i;
        }
      }
    },
    async findNav(id) {
      this.loading = true;
      const data = await this.$api.findNav(id);
      this.data = data;
      this.loading = false;
    }
  },
  async asyncData() {
    const { data: categorys } = await axios.get('/api/category/list')

    const id = categorys[0] && categorys[0].children[0] && categorys[0].children[0]._id
    const websites = await axios.post('/api/nav/find', {
      id
    })
    return {
      categorys,
      data: websites
    }
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        if (window.screenWidth < 481) {
          this.isLeftbar = false
        } else {
          this.isLeftbar = true
        }
      })()
    }
    window.onresize()
  }
};
