export default {
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    type: {
      validator: function(value = 'create') {
        // 这个值必须匹配下列字符串中的一个
        return ['create', 'update'].indexOf(value) !== -1
      },
    },
    item: Object,
  },
  data() {
    return {
      categorys: [],
      form: {
        url: '',
        categoryId: '',
        name: '',
        desc: '',
      },
      rules: {
        url: [
          { required: true, message: '请输入url', trigger: 'blur' },
          {
            pattern: /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/,
            message: '请输入正确的url',
          },
        ],
        categoryId: [
          { required: true, message: '请选择分类', trigger: 'blur' },
        ],
        name: [],
        desc: [],
      },
      isError: false,
    }
  },
  methods: {
    async getCategorys() {
      const { data } = await this.$api.getCategoryList()
      this.categorys = data
    },
    async addNav(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 判断编辑还是更新
          if (this.type === 'update') {
            const id = this.item._id
            const res = await this.$api.editNav({
              id,
              ...this.form
            })
          } else {
            const res = await this.$api.addAudit(this.form)
            if (res.msg) {
              this.$message.error(`${res.msg}，请手动输入！`)
              this.isError = true
              return false
            }
            this.$message(`感谢您的提交，请等待后台审核通过！`)
          }
          this.form = {}

          this.$emit('update:show', false)
          this.$emit('submit')
        } else {
          return false
        }
      })
    },
  },
  watch: {
    item(ite) {
      this.isError = true
      this.form.name = ite.name
      this.form.url = ite.href
      this.form.desc = ite.desc
      this.form.categoryId = ite.categoryId
    },
  },
  created() {
    this.getCategorys()
  },
}
